import React from "react";
import s from "./users.module.css";
import {PhotosType} from "../../redux/redux-store";
import {NavLink} from "react-router-dom";


type UserItemType = {
    id: number
    photos: PhotosType
    followed: boolean
    name: string | null
    status: string | null
    isFollowingInProgress: number[]
    follow: (userId: number) => void
    unfollow: (userId: number) => void
}


export const UserItem: React.FC<UserItemType> = (props) => {


    const follow = () => {
        props.follow(props.id)
    }

    const unfollow = () => {
        props.unfollow(props.id)
    }

    return <div className={s.content__item} key={props.id}>
        <div className={s.content__item_leftBlock}>
            <div className={s.content__item_leftBlock_img}>
                <NavLink to={'/profile/' + props.id}>
                    <img
                        src={(props.photos.small) ? props.photos.small : "https://i.pinimg.com/originals/1d/a5/a0/1da5a0d16d1f7dd7e43b6da4b68cdf46.png"}
                        alt=""/>
                </NavLink>
            </div>
            <div className={s.content__item_leftBlock_button}>
                {(props.followed) ? <button onClick={unfollow} disabled={props.isFollowingInProgress.some(id => id == props.id)}>unfollow</button> :
                    <button onClick={follow} disabled={props.isFollowingInProgress.some(id => id == props.id)}>follow</button>}
            </div>
        </div>
        <div className={s.content__item_rightBlock}>
            <div className={s.content__item_rightBlock_name}>
                <span>{props.name}</span>
            </div>
            <div className={s.content__item_rightBlock_status}>
                <span>{props.status}</span>
            </div>
            <div className={s.content__item_rightBlock_location}>
                <span>{'some city'}</span>
                <span>{'some country'}</span>
            </div>
        </div>
    </div>
}

